  border: 0;
  /* 1 */
  padding: 0;
  /* 2 */ }

/**
   * Remove default vertical scrollbar in IE 8/9/10/11.
   */
textarea {
  overflow: auto; }

/**
* Don't inherit the `font-weight` (applied by a rule above).
* NOTE: the default cannot safely be changed in Chrome and Safari on OS X.
*/
optgroup {
  font-weight: bold; }

/* Tables
   ========================================================================== */
/**
* Remove most spacing between table cells.
*/
table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

/**
 *  @file
 *  Sass Vars
 */
/**
 *  @file
 *  Susy config declaration
 */
head:before {
  position: absolute;
  top: 70px;
  right: 0; }

/**
 *  @file
 *  Layouts
 *  Define the initial layout of the regions in page.tpl.php
 */
.layout-container {
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto;
  background: green; }
  .layout-container:after {
    content: " ";
    display: block;
    clear: both; }
  head {
    display: block;
    position: fixed;
    right: 10px;
    top: 10px;
    z-index: 999;
    color: #333;
    background: rgba(255, 255, 255, 0.25); }
  head:before {
    content: "|||";
    display: block;
    padding: 5px 10px;
    font-family: sans-serif;
    font-size: 16px;
    font-weight: bold; }
  head:hover {
    background: rgba(255, 255, 255, 0.5);
    color: red; }
  head:hover ~ .layout-container,
  head:hover ~ body .layout-container {
    position: relative; }
    head:hover ~ .layout-container:before,
    head:hover ~ body .layout-container:before {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      content: " ";
      z-index: 998;
      background-image: -webkit-linear-gradient( left , rgba(102, 102, 255, 0.25), rgba(179, 179, 255, 0.25) 80%, transparent 80%), -webkit-linear-gradient( top , #000 1px, transparent 1px);
      background-image: linear-gradient(to right, rgba(102, 102, 255, 0.25), rgba(179, 179, 255, 0.25) 80%, transparent 80%), linear-gradient(to bottom, #000 1px, transparent 1px);
      background-size: 8.47458%, 100% 24px;
      background-origin: content-box, border-box;
      background-clip: content-box, border-box;
      background-position: left top; }
  .layout-container::after {
    clear: both;
    content: "";
    display: table; }
  @media (min-width: 44em) {
    .layout-container {
      background: red; } }
  @media (min-width: 64em) {
    .layout-container {
      background: yellow; } }
  @media (min-width: 75em) {
    .layout-container {
      background: blue; } }

