/**
 *  @file
 *  Layouts
 *  Define the initial layout of the regions in page.tpl.php
 */

.layout-container {
  @include container;
  @include clearfix;
  background: green;
  @include breakpoint($tablet) {
    background: red; }
  @include breakpoint($desk) {
    background: yellow; }
  @include breakpoint($max-width) {
    background: blue; } }

.layout-one-sidebar.layout-sidebar-first {
  .layout-content {
    @include breakpoint($tablet) {
      @include span(9 of 12 last); } }
  .layout-sidebar-first {
    @include breakpoint($tablet) {
      @include span(3 of 12 first); } } }

.layout-one-sidebar.layout-sidebar-second {
  .layout-content {
    @include breakpoint($tablet) {
      @include span(9 of 12 first); } }
  .layout-sidebar-second {
    @include breakpoint($tablet) {
      @include span(3 of 12 last); } } }

.layout-two-sidebars {
  .layout-content {
    @include breakpoint($tablet) {
      @include span(9 of 12 last); }
    @include breakpoint($desk) {
      @include span(isolate 6 at 4 of 12); } }
  .layout-sidebar-first {
    @include breakpoint($tablet) {
      @include span(3 of 12 first); } }
  .layout-sidebar-second {
    @include breakpoint($tablet) {
      float: none;
      clear: both; }
    @include breakpoint($desk) {
      @include span(3 of 12 last);
      clear: none; } } }
